import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex)

export default new Vuex.Store({
    state:{
        num:0,
        vs:"",
        sp:[],
        good:[]
    },
    getters:{
        getShopNum(state){
            return state.num
        },
        getVS(state){
            return state.vs
        },
        getShop(state){
            return state.sp
        },
        getgood(state){
            return state.good
        }
    },
    mutations:{
        addShopNum(state,num){
            // console.log(num)
            state.num += num;
        },
        changeShopNum(state,num){
            state.num=num;
        },
        showVs(state,br){
            // console.log(br)
            state.vs = br;
        },
        getShop(state,sp){
                state.sp.push(sp);
        },
        setgood(state,good1){
            console.log(good1)
                state.good=good1;
        },
    },
    actions:{
        addShopNumByAction({commit},num){
            commit('addShopNum',num)
        },
        changeShopNumByAction({commit},num){
            commit('changeShopNum',num)
        },
        showVsByAction({commit},br){
            commit('showVs',br)
        },
        spByAction({commit},sp){
            commit('getShop',sp)
        },
        goodByAction({commit},good1){
            commit('setgood',good1)
        },

    }
})