<template>
    <div>
        <van-card
        v-for="(item,index) in detail"
        :key="index"
        :num="item.num"
        :price="item.price"
        :title="item.cont"
        :thumb="item.url"
        >
            <div slot="footer">
                <van-button size="mini"></van-button>
                <van-button size="mini" @click="del(index)"></van-button>
            </div>
        </van-card>
        <router-link to="/Shop" class="link">
            <h3></h3>
        </router-link>
        <div class="bottom12"></div>
    </div>
</template>

<script>
export default {
    name:'Shopping',
    data(){
        return {
            goods:[],
        }
    },
    created(){
        this.goods = JSON.parse(localStorage.getItem("sp"))  
        console.log(this.goods)
    },
    computed:{
        detail(){
            return this.$store.state.sp
        }
    },
    methods:{
        del(i){
            // console.log(this.$store.state.sp)
            // console.log(this.$store.state.sp[0][1])
            this.$store.dispatch("addShopNumByAction",-this.goods[i].num)
            this.goods.splice(i,1)
            localStorage.setItem("sp",JSON.stringify(this.goods))
            this.$store.state.sp=[]
        }
    },
    
}
</script>
<style scoped>
    .bottom12{
    float: left;
    width: 100%;
    height: 5.2rem
    }
    .link{
        font-size: 1.5rem;
        line-height: 2.8rem;
        font-family: 'Courier New', Courier, monospace;
        text-align: center;
        margin-top: 2rem;
        color: darkslateblue
    }

</style>
